.TicketListPage {
  min-height: 100vh;
  padding-bottom: 24px;
  padding-left: 20px;

  .number-item {
    background-color: #9EA3AC;
    color: #FFFFFF;
    font-weight: 600;
    width: 17px;
    height: 17px;
    line-height: 17px;
    text-align: center;
    border-radius: 3px;
    left: 50%;
  }

  .tickets-table {
    margin-top: 16px;
    max-width: 89vw;

    .tickets-table-stats {
      margin-top: 16px;
      margin-bottom: 16px;

      .tickets-all {
        font-size: 1.2rem;
        margin-right: 8px;
        font-weight: bold;
        color: rgb(65, 182, 127);
      }

      .tickets-open {
        font-size: 1.2rem;
        margin-right: 8px;
        font-weight: bold;
        color: rgb(65, 182, 127);
      }
      .tickets-pending {
        font-size: 1.2rem;
        margin-right: 8px;
        font-weight: bold;
        color: rgb(248, 179, 68);
      }
      .tickets-resolved {
        font-size: 1.2rem;
        margin-right: 8px;
        font-weight: bold;
        color: rgb(127, 58, 171);
      }
      .tickets-closed {
        font-size: 1.2rem;
        margin-right: 8px;
        font-weight: bold;
        color: rgb(41, 177, 195);
      }
      .tickets-default {
        font-size: 1.2rem;
        margin-right: 8px;
        font-weight: bold;
        color: rgba(39,46,76,0.5);
      }
      .ticket-label {
        text-decoration: underline;
        cursor: pointer;
        color: rgba(39,46,76,0.5);
      }
      .ticket-label-active {
        text-decoration: underline;
        cursor: auto;
        color: inherit;
      }
    }
  }

  .ticket-list-table {
    .MuiTableRow-head {
      padding: 0 10px;
    }
    .MuiTableCell-head {
        font-size: 14px !important;
        font-weight: 600;
        color: #000000;
        background-color: #DDDFE3;
        padding: 10px 5px 5px 5px !important;
    }
    .MuiTableCell-body {
      font-size: 12px;
    }
  }
}
