.ServicingInstructions {
    font-family: 'usual';
    font-weight: 600;

    .page-tab-title {
        background-color: #F5F7FB;
    }

    .tab-title-shadow {
        width: inherit;
        height: 2px;
        background-image: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0));
    }

    .page-tab-content {
        background-color: #F5F7FB;
        padding-top: 48px;
        padding-bottom: 48px;
        padding-left: 24px;
        padding-right: 24px;
        min-height: 500px;
    }

    .si-printing-of-card-sample-img {
		width: 100%;
        border: 1px solid #DDDFE3;
    }
    
    .draggable {
        touch-action: none;
        cursor: all-scroll;
    }
     
    .dragged {
        opacity: 0.7;
    }
     
    .placeholder {
      opacity: 0.5;
    }

    .no-drag {
        cursor: auto;
    }
    
}